import { Transaction } from "@mysten/sui/transactions";
import { PACKAGE_ID, REGISTRY } from '../config/constants';
import { useWallet } from '@suiet/wallet-kit';

/**
 * Hook to create a raw transaction for minting an NFT
 * @returns A function to execute a mint NFT transaction.
 */
export function useMintNFT() {
    const wallet = useWallet();

    function selectModuleID(variant) {
        const modules = {
            "8x": '0x8',
            "16x": '0x16',
            "32x": '0x32',
            "64x": '0x64'
        };
        return modules[variant] || modules["8x"];
    }

    const mintNFT = async (gridSize, variant = "8x") => {
        if (!wallet.connected || !wallet.account) {
            console.error('Wallet not connected');
            return;
        }

        try {
            // Create a new transaction block
            const tx = new Transaction();

            // Let the wallet handle gas coin selection, but provide NFT payment amount // Adjust as needed (1.5 SUI in base units)
            const amt = 2000000;
            const [coin] = tx.splitCoins(tx.gas, [tx.pure.u64(amt)]);

            // This Transfer will work if uncommented
            // const address = '0x62db89b80d8f4cb32bad7e4eacbea787b21543469a58172393cc4be7f4c74de9';
            // tx.transferObjects([coin], tx.pure.address(address))

            // Add the Move call to the transaction
            const moduleID = selectModuleID(variant);
            tx.moveCall({
                target: `${PACKAGE_ID}::rnfts::main::new`,
                arguments: [
                    tx.pure.u64(gridSize),             // Grid size as u64
                    tx.object(REGISTRY),                  // Registry object ID
                    tx.object(moduleID),     // Dynamically choose the module ID
                    coin                       // Payment coin for the NFT
                ],
            });

            // Return any remaining coin to the sender after the Move call
            tx.mergeCoins(tx.gas, [coin]);

            console.log(tx.serialize())
            // // Pass the TransactionBlock instance directly to the wallet method
            const result = await wallet.signAndExecuteTransaction({
                transaction: tx,  // Pass the instance directly without serialize()
            });

            // Wait for the transaction to settle and log the result
            await new Promise((resolve) => setTimeout(resolve, 500));
            if (result) {
                const txId = result.digest;
                console.log('Transaction successful:', `https://explorer.sui.io/txblock/${txId}?network=testnet`);
            } else {
                console.error('Transaction failed or result is unexpected:', result);
            }
        } catch (error) {
            console.error('Error while interacting with the wallet:', error);
            if (error?.message) {
                console.error('Wallet error message:', error.message);
            }
        }
        
    
    };

    return { mintNFT };
}
